	module.exports = {"data":{"markdownRemark":{"html":"<h2>Intent</h2>\n<p>As we move forward the problems will be getting less structured but with specific goals in mind. This step is to accomplish a few things:</p>\n<ul>\n<li>Putting together a new service of their own design using all the previously learned methods.</li>\n<li>Learning how painful coupled API calls can be while trying to keep data in sync across multiple data stores. (We will attempt to alleviate this in the next step)</li>\n<li>Figuring out where the communication logic will sit in the established API.</li>\n</ul>\n<h2>Problem</h2>\n<p>When an item has its quantity drop to zero, communicate with a separate service that will then ‘Order’ more stock and then inform the API that more stock has been delivered. The API will then record the new quantity of stock. </p>\n<p>The new service should communicate with your API using coupled, synchronous calls. I.e. TCP or REST calls. The new service should also have it's own datastore. It doesn't need to be the same type of datastore as the established API, but it can be. </p>\n<p>Try to make your calls transactional so your services are in sync; consider any failure scenarios. </p>\n<p>As in the previous steps, use TDD and your choice of either CRUD or CQRS.\nThe new service can be an API or a service application. That’s up to you.</p>\n<p><a href=\"../shopping4\">&#x3C; Prev</a> | <a href=\"../shopping6\">Next ></a></p>","frontmatter":{"path":"/shopping5","title":"Shopping Cart 5: Distributed"}}},"pathContext":{}}

